.menu {
  margin-left: 60px;

  @include tablet {
    margin-left: 35px;
  }

  @include small-tablet {
    margin-left: 10px;
  }

  &__item {
    // position: relative;
  }
}

.menu__btn {}

.dropdown {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.9);
  box-shadow: 0px 4px 70px rgba(0, 0, 24, 0.07);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  margin: 0 auto;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;

  &::before {
    content: "";
    background: rgba(255, 255, 255, 0.9);
    box-shadow: 0px 4px 70px rgb(0 0 24 / 7%);
    backdrop-filter: blur(20px);
  }
}

.dropdown--active {
  opacity: 1;
  visibility: visible;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 4px 70px rgb(0 0 24 / 7%);
  backdrop-filter: blur(20px);
}

.dropdown__link {
  display: block;
  width: 100%;
  padding: 10px 10px;
  color: #fff;
}

.dropdown__link-list {
  display: flex;
  flex-wrap: wrap;

  @include tablet {
    flex-direction: column;
  }
}

.dropdown__link-list__item {
  width: 33.33%;
  margin-bottom: 25px;

  @include tablet {
    width: 100%;
    margin-bottom: 15px;
  }
}

.dropdown__link-list__link {
  font-weight: 500;
  font-size: 20px;
  line-height: 36px;
  color: #000018;

  @include tablet {
    font-size: 16px;
    line-height: 25px;
  }
}

.accordion {
  margin-bottom: 20px;
}

.accordion__list {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding-left: 50px;
  padding-right: 50px;
  height: 400px;
  overflow: auto;
  margin-top: 110px;

  @include small-tablet {
    margin-top: 80px;
  }
}

.accordion__control {
  width: 100%;
  background-color: transparent;
  cursor: pointer;
  text-align: left;
}

.accordion__content {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0 20px;
  will-change: max-height;
  transition: all 0.3s ease-out;
  box-sizing: content-box;
}

.accordion__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: #000018;

  @include tablet {
    font-size: 18px;
  }
}

.accordion__icon {
  transition: transform 0.3s ease-out;
}

.open .accordion__icon {
  transform: rotate(45deg);
}

.open .accordion__content {
  opacity: 1;
  padding: 20px;
}

.dropdown__svg {
  position: absolute;
  right: 50px;
  top: 25px;

  @include small-tablet {
    right: 35px;
    top: 15px;
  }

  & svg {
    stroke: #313188;
    stroke-width: 3px;
    width: 37px;
    height: 37px;
  }
}


