.products {
  padding-top: 100px;

  @include small-tablet {
    padding-top: 45px;
  }

  &__list {
    padding-top: 50px;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 31px;

    @include big-desktop {
      padding-top: 40px;
      gap: 26px;
    }

    @include tablet {
      gap: 38px 20px;
    }
  }

  &__item {
    background-color: var(--nor-btn);
    box-shadow: 3px 3px 10px rgba(0, 0, 24, 0.07);
    border-radius: 30px;
    padding: 30px 45px;
    grid-column: span 6;
    display: flex;

    @include big-desktop {
      padding-right: 20px;
    }

    @include desctop {
      padding: 25px 20px;
      flex-direction: column;
    }

    @include small-tablet {
      grid-column: span 12;
    }
  }

  &__pic {
    flex: 1 0 auto;
    margin-right: 40px;

    @include big-desktop {
      margin-right: 22px;
    }

    @include tablet {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__image {
    border-radius: 30px;

    @include big-desktop {
      width: 269px;
      height: 195px;
    }

    @include tablet {
      width: 319px;
    }

    @include small-tablet {
      width: 258px;
      height: 157px;
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--color-text);
    margin: 0;

    @include big-desktop {
      margin-bottom: 10px;
    }
  }

  &__date {
    font-size: 18px;
    line-height: 36px;
    color: var(--color-text);
    opacity: 0.4;

    @include tablet {
      margin-bottom: 45px;
    }
  }

  &__btn {
    font-weight: 500;
    font-size: 18px;
    line-height: 36px;
    color: var(--img-color);
    margin-top: auto;
    display: inline-block;
    position: relative;
    transition: opacity .3s ease-in ;

    &:hover {
      opacity: .7;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 10px;
      left: 125px;
      width: 50px;
      height: 12px;
      background-image: url(../img/catalog-arr.svg);
      background-repeat: no-repeat;
      background-size: cover;

      @include big-desktop {
        bottom: 12px;
        right: 25px;
      }
    }
  }
}

.products-slider {
  margin-bottom: 85px;
  padding-bottom: 85px;

  @include big-desktop {
    margin-bottom: 65px;
    padding-bottom: 65px;
  }
}
