.footer {
  background-color: var(--text-color);
  padding-top: 42px;
  padding-bottom: 10px;

  @include tablet {
    padding-top: 16px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    position: relative;

    @include big-desktop {
      margin-bottom: 17px;
    }

    @include tablet {
      flex-wrap: wrap;
      margin-bottom: 90px;
    }

    @include mobile {
      margin-bottom: 145px;
    }
  }

  &__item {

    &:first-child {
      max-width: 275px;
      width: 100%;
      margin-right: 95px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      @include big-desktop {
        max-width: 205px;
      }

      @include small-tablet {
        max-width: 100%;
        margin-right: 0;
        margin-bottom: 42px;
      }
    }

    &:last-child {
      max-width: 455px;
      width: 100%;

      @include big-desktop {
        max-width: 370px;
      }

      @include tablet {
        margin-top: 35px;
        max-width: 100%;
        display: flex;
      }

      @include mobile {
        flex-direction: column;
      }
    }

    &--flex {
      flex: 1 1 auto;
    }
  }

  &__item-left {
    margin-bottom: 40px;

    @include tablet {
      margin-bottom: 30px;
    }
  }

  &__soc {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: absolute;
    left: 0;
    bottom: 35px;

    @include big-desktop {
      bottom: 5px;
    }

    @include tablet {
      bottom: -85px;
      flex-direction: row;
    }

    @include mobile {
      flex-wrap: wrap;
      bottom: -125px;
      flex-direction: column;
    }
  }

  &__item-logo {
    fill: var(--light-color);
    width: 141px;
    height: 50px;
    margin-bottom: 15px;

    @include tablet {
      width: 94px;
      height: 25px;
      flex: 1 0 auto;
    }
  }

  &__text-logo {
    font-size: 16px;
    line-height: 22px;
    color: var(--light-color);
    margin: 0;

    @include small-tablet {
      font-size: 14px;
      margin-left: 20px;
    }
  }

  &__subtext {
    font-size: 18px;
    line-height: 28px;
    color: var(--light-color);
    margin: 0;
    font-style: normal;
    margin-bottom: 15px;

    @include tablet {
      font-size: 16px;
      margin-bottom: 10px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 18px;
    line-height: 36px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 10px;

    @include big-desktop {
      font-size: 16.5px;
    }

    @include tablet {
      font-size: 16px;
    }
  }

  &__map {
    margin-top: 35px;
    margin-bottom: 45px;
    padding-left: 55px;
    display: block;
    position: relative;
    transition: opacity .3s ease-in;

    &:hover {
      opacity: 0.7;
    }

    @include big-desktop {
      margin-top: 0;
      margin-bottom: 35px;
    }

    @include tablet {
      margin-top: 25px;
      margin-bottom: 0;
    }

    @include mobile {
      margin-bottom: 25px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 34px;
      height: 30px;
      background-image: url(../img/map.svg);
      background-repeat: no-repeat;
      background-size: 34px 30px;
    }
  }

  &__video {
    padding-left: 55px;
    display: block;
    position: relative;

    @include tablet {
      padding-left: 50px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 30px;
      height: 30px;
      background-image: url(../img/video.svg);
      background-repeat: no-repeat;
      background-size: 30px 30px;
    }
  }

  &__item-descr {
    display: flex;
    justify-content: space-between;

    @include big-desktop {
      flex-direction: column;
    }
  }

  &__item-address {

    @include tablet {
      margin-right: 70px;
    }

    @include mobile {
      margin-right: 0;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 2px solid rgba(49, 49, 136, 0.1);
    padding-top: 3px;

    @include tablet {
      padding-top: 10px;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__bottom-text {
    font-family: "PTSansCaption", sans-serif;
    font-weight: 400;
    font-size: 10px;
    line-height: 13px;
    color: var(--light-color);

    @include mobile {
      margin: 0;
      margin-bottom: 5px;
    }

  }

  &__link {

    @include small-tablet {
      display: flex;
      align-items: center;
    }
  }
}

.social {
  display: flex;
  align-items: center;

  &--bg {
    position: relative;
    padding: 14px 36px;

    @include tablet {
      padding: 9px 33px;
      margin-left: 48px;
    }

    @include mobile {
      margin-left: 0;
    }

    &::before {
      content: "";
      opacity: 0.1;
      background-color: var(--img-color);
      position: absolute;
      top: 0;
      left: 0;
      border-radius: 30px;
      height: 100%;
      width: 100%;
    }
  }

  &__item {
    position: relative;
    z-index: 1;

    &:not(:last-child) {
      margin-right: 30px;

      @include tablet {
        margin-right: 23px;
      }
    }
  }

  &__link {
    width: 30px;
    height: 30px;
    background-repeat: no-repeat;
    background-size: 30px 30px;
    display: block;
    transition: opacity .3s ease-in;

    &:hover {
      opacity: 0.4;
    }

    @include tablet {
      width: 25px;
      height: 23px;
      background-size: 25px 23px;
    }
  }

  &__link--vk {
    background-image: url(../img/vk.svg);
  }

  &__link--yt {
    background-image: url(../img/yt.svg);
  }

  &__link--tg {
    background-image: url(../img/tg.svg);
  }

  &__link--wa {
    background-image: url(../img/wa.svg);
  }

  &__link--vb {
    background-image: url(../img/vb.svg);
  }

  &--tel {
    margin-top: 15px;

    @include tablet {
      margin-top: 5px;
    }
  }
}

.nav {
  display: flex;
  justify-content: flex-start;

  @include tablet {
    margin-bottom: 15px;
  }

  &__list {

    &:first-child {
      margin-right: 130px;

      @include big-desktop {
        margin-right: 90px;
      }

      @include tablet {
        margin-right: 145px;
      }

      @include mobile {
        margin-right: 55px;
      }
    }
  }

  &__item {
    margin-bottom: 20px;

    @include tablet {
      margin-bottom: 5px;
    }
  }

  &__link {
    font-weight: 500;
    font-size: 18px;
    line-height: 36px;
    color: var(--light-color);
    transition: opacity .3s ease-in;

    &:hover {
      opacity: 0.7;
    }

    @include tablet {
      font-size: 16px;
    }
  }
}
