.stepper {
  position: relative;
  display: flex;
  align-items: center;
  width: 150px;
  height: 50px;
  user-select: none;
  background: rgba(255, 255, 255, 0.8);
  border: 1px solid #D9D9D9;
  border-radius: 30px;
  padding: 0 20px;
  margin-top: 55px;

}

.stepper__field {
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  width: 100%;
  height: 100%;
  justify-content: center;
}

.stepper__input {
  border: none;
  width: 10px;
  height: 100%;
  outline: none;
  font-weight: 700;
  min-width: 2ex;
  font-weight: 400;
  font-size: 18px;
  line-height: 36px;
  color: #000018;
  flex-grow: 1;
  text-align: center;
}

.stepper__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  height: 50%;
  background-color: transparent;
  cursor: pointer;
  font-size: 40px;
}

.stepper__btn--disabled {
  opacity: 0.5;
  pointer-events: none;
}
