.lk {
  padding: 150px 90px 120px;

  @include large-desktop {
    padding: 150px 0 120px;
  }

  @include desctop {
    padding: 140px 0 120px;
  }

  @include mobile {
    padding: 85px 0 55px;
  }
}

.lk-tabs {
  display: flex;
  align-items: flex-start;
  position: relative;

  @include desctop {
    flex-direction: column;
  }

  &__side {
    display: inline-flex;
    align-items: flex-start;
    flex-direction: column;
    background-color: var(--lk-color);
    box-shadow: 3px 3px 10px rgba(0, 0, 24, 0.07);
    border-radius: 30px;
    padding: 35px 45px 10px;
    margin-left: -45px;
    margin-right: 35px;
    max-width: 375px;
    width: 100%;

    @include large-desktop {
      max-width: 315px;
    }

    @include desctop {
      max-width: 100%;
      margin-left: 0;
      margin-right: 0;
      padding: 35px 15px 10px;
      margin-bottom: 45px;
    }

    @include mobile {
      padding: 25px 15px 10px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--text-color);
    margin: 0;
    margin-bottom: 10px;

    @include desctop {
      margin-bottom: 0;
    }

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__cash {
    background-color: #FFE600;
    border-radius: 30px;
    padding: 7px 14px;
    font-weight: 400;
    font-size: 11px;
    color: var(--text-color);
    margin: 0;
    margin-bottom: 5px;
    display: inline-block;

    @include big-desktop {
      font-size: 10px;
    }

    @include mobile {
      font-size: 12px;
    }
  }

  &__mail {
    font-weight: 400;
    font-size: 18px;
    line-height: 36px;
    color: var(--text-color);
    margin-bottom: 20px;
    position: relative;

    @include desctop {
      display: block;
      width: 100%;
      bottom: 0;
    }

    &::before {
      content: "";
      border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
      width: 145%;
      height: 2px;
      position: absolute;
      bottom: -20px;
      left: 0;

      @include desctop {
        width: 100%;
      }
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    width: 100%;

    @include desctop {
      flex-direction: row;
      justify-content: space-between;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__nav-item {
    padding: 20px 0;

    @include desctop {
      padding: 5px 0;
    }

    @include mobile {
      padding: 14px 0;
    }

    &:not(:last-child) {
      position: relative;

      &::before {
        content: "";
        border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
        width: 100%;
        height: 2px;
        position: absolute;
        bottom: 0;
        left: 0;

        @include desctop {
          display: none;
        }

        @include mobile {
          display: block;
        }
      }
    }
  }

  &__nav-btn {
    font-weight: 500;
    font-size: 18px;
    line-height: 36px;
    color: var(--text-color);
  }

  & .tabs__nav-btn--active {
    color: var(--img-color);


    &:focus {
      color: var(--img-color);
      background-color: transparent;
    }
  }

  &__content {
    width: 100%;
    min-width: 0;
  }

  &__panel-title {
    font-weight: 700;
    font-size: 32px;
    line-height: 36px;
    color: var(--text-color);
    margin: 0;
    margin-bottom: 55px;
    padding-top: 15px;

    @include large-desktop {
      margin-bottom: 40px;
    }

    @include big-desktop {
      margin-bottom: 30px;
    }

    @include desctop {
      font-size: 28px;
      margin-right: 15px;
    }

    @include mobile {
      margin-right: 0;
      margin-bottom: 14px;
      font-size: 24px;
    }
  }

  &__descr {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
    }
  }
}

.order {

  &__details {
    margin-bottom: 38px;
    padding: 18px 35px;

    @include large-desktop {
      padding: 18px 0 35px 30px;
    }

    @include desctop {
      padding: 25px 0 35px 15px;
    }

    &:last-child {
      margin-bottom: 75px;

      @include large-desktop {
        margin-bottom: 35px;
      }
    }

    &:nth-child(even) {
      background: rgba(242, 242, 250, 0.4);
      border-radius: 30px;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    @include small-tablet {
      flex-direction: column;
      position: relative;
    }
  }

  &__select {
    position: relative;
    max-width: 456px;
    width: 100%;

    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 50%;
      right: 35px;
      z-index: 1;
      background-image: url(../img/arr-select.svg);
      background-repeat: no-repeat;
      background-size: 24px 24px;
      width: 24px;
      height: 24px;
      transform: translateY(-50%);
      pointer-events: none;

      @include mobile {
        right: 15px;
      }
    }
  }

  &__sort {
    background: #F2F2FA;
    box-shadow: 3px 3px 10px rgba(0, 0, 24, 0.07);
    border-radius: 30px;
    border: none;
    font-size: 18px;
    line-height: 22px;
    color: #000018;
    padding: 24px 68px 24px 35px;
    appearance: none;
    -webkit-appearance: none;
    display: block;
    width: 100%;

    @include mobile {
      font-size: 16px;
      padding: 20px 68px 20px 15px;

    }
  }

  &__sort-option {}

  &__sort-color {}

  &__descr {}

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--text-color);
    margin: 0;
    display: inline-block;
    margin-right: 10px;
    margin-bottom: 15px;

    @include mobile {
      font-size: 20px;
      padding-top: 17px;
      margin-bottom: 0;
    }
  }

  &__quantity {
    font-size: 18px;
    line-height: 36px;
    margin: 0;
    color: #D9D9D9;

    @include mobile {
      display: block;
      font-size: 16px;
      line-height: 1.2;
      margin-bottom: 10px;
    }
  }

  &__time {
    font-size: 16px;
    line-height: 32px;
    color: var(--text-color);
    margin: 0;
  }

  &__time--weight {
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;

    @include mobile {
      display: block;
    }
  }

  &__descr-sum {
    padding-top: 30px;
    padding-left: 80px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @include large-desktop {
      padding-top: 25px;
      padding-left: 10px;
    }

    @include small-tablet {
      align-items: flex-start;
    }

    @include mobile {
      padding-top: 10px;
      padding-left: 0;
    }
  }

  &__sum {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: var(--text-color);
    margin: 0;
  }

  &__btn {
    background: #313188;
    border-radius: 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    padding: 15px 30px;
    color: #FFFFFF;
    margin-top: 30px;
    margin-right: 37px;

    @include large-desktop {
      margin-right: 0;
    }

    @include desctop {
      font-size: 0;
      background-image: url(../img/redo.svg);
      background-repeat: no-repeat;
      background-position: 50%;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      padding: 12px;
      margin-right: 15px;
    }

    @include small-tablet {
      position: absolute;
      bottom: 0;
      right: 0;
    }

    @include mobile {
      bottom: 25px;
    }
  }

  &__more {}

  &__more-btn {
    font-weight: 500;
    font-size: 16px;
    line-height: 36px;
    color: #FF0000;
    padding-left: 40px;
    position: relative;

    @include mobile {
      font-size: 14px;
      padding-left: 30px;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 37%;
      left: 0;
      z-index: 1;
      background-image: url(../img/arr-det.svg);
      background-repeat: no-repeat;
      background-size: 15px 15px;
      width: 15px;
      height: 15px;
      transform: translateY(-50%);
      pointer-events: none;
      transform: rotate(180deg);
    }
  }

  &__list {
    counter-reset: section;
  }

  &__item {
    display: flex;
    align-items: center;
    padding: 30px 35px 20px 80px;
    position: relative;

    @include large-desktop {
      padding: 30px 0 20px 80px;
    }

    @include desctop {
      padding: 30px 0 20px 30px;
    }

    @include small-tablet {
      padding: 17px 0 10px 0px;
    }

    &::before {
      counter-increment: section;
      content: counter(section);
      font-weight: 700;
      font-size: 24px;
      line-height: 36px;
      color: #D9D9D9;
      position: absolute;
      top: 45%;
      left: 10px;

      @include large-desktop {
        top: 36%;
        left: 5px;
      }

      @include mobile {
        display: none;
      }
    }

    &:not(:last-child) {

      &::after {
        content: "";
        border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
        width: 68%;
        height: 2px;
        position: absolute;
        bottom: 0;
        right: 31px;

        @include large-desktop {
          right: 0;
        }
      }
    }
  }

  &__item-pic {
    flex: 1 0 auto;

    @include mobile {
      flex: 1 1 auto;
    }
  }

  &__item-content {
    display: flex;


    @include small-tablet {
      flex-direction: column;
    }
  }

  &__item-wrap {
    padding: 0px 80px 0 0;
    max-width: 530px;

    @include large-desktop {
      padding: 0 35px 0 25px;
    }

    @include desctop {
      padding: 0 65px 0 25px;
    }

    @include mobile {
      padding: 0 0 0 15px;
      display: flex;
      flex-direction: column;
      margin-bottom: 25px;
    }
  }

  &__item-title {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    color: #000018;
    margin: 0;
    margin-bottom: 17px;

    @include large-desktop {
      padding-top: 5px;
    }

    @include desctop {
      font-size: 16px;
    }

    @include mobile {
      font-size: 14px;
      line-height: 26px;
      margin-bottom: 10px;
    }
  }

  &__item-descr {
    display: flex;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
    }
  }

  &__item-price {
    font-weight: 700;
    font-size: 18px;
    line-height: 36px;
    color: #000018;
    margin: 0;

    @include mobile {
      font-size: 20px;
      margin-bottom: 10px;
    }

  }

  &__item-quant {
    font-weight: 400;
    font-size: 16px;
    line-height: 32px;
    color: #000018;
    margin: 0;
    margin-right: 75px;

    @include large-desktop {
      margin-right: 0;
    }
  }

  &__item-quant-weight {
    font-weight: 700;
    font-size: 18px;
    line-height: 32px;
  }

  &__item-sum {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    flex: 1 0 auto;

    @include desctop {
      margin-right: 15px;
    }

    @include mobile {
      margin-right: 0;
      margin-left: 15px;
    }
  }
}

.lk-card {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;

  @include tablet {
    flex-direction: column;
    margin-bottom: 40px;
  }

  @include mobile {
    margin-bottom: 75px;
  }

  &--margin {
    margin-bottom: 425px;

    @include desctop {
      margin-bottom: 65px;
    }

    @include tablet {
      margin-bottom: 45px;
    }

    @include mobile {
      margin-bottom: 15px;
    }
  }

  &__list {
    flex: 1 1 100%;

    @include tablet {
      margin-bottom: 15px;
    }

    &:nth-child(odd) {
      margin-right: 90px;

      @include big-desktop {
        margin-right: 70px;
      }

      @include tablet {
        margin-right: 0;
      }
    }
  }

  &__item {
    font-weight: 400;
    font-size: 16px;
    line-height: 32px;
    color: #000018;
    display: flex;
    justify-content: space-between;
    position: relative;

    @include mobile {
      flex-direction: column;
    }

    &::before {
      content: "";
      border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
      width: 100%;
      height: 2px;
      position: absolute;
      bottom: -10px;
      left: 0;
    }

    &:not(:last-child) {
      margin-bottom: 15px;

      @include mobile {
        margin-bottom: 25px;
      }
    }
  }

  &__item-weight {
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: #000018;

    @include big-desktop {
      margin-left: 5px;
    }

    @include mobile {
      margin-left: 0;
    }
  }

  &__subtitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-bottom: 15px;

    @include tablet {
      font-size: 22px;
    }

    @include mobile {
      font-size: 20px;
    }
  }
}

.f-end {
  justify-content: flex-end;
}

.address-slider {
  position: absolute;
  left: -45px;
  bottom: -25px;
  width: 100%;
  background: #F2F2FA;
  box-shadow: 3px 3px 10px rgba(0, 0, 24, 0.07);
  border-radius: 30px;
  padding: 35px 46px 90px;

  @include desctop {
    position: relative;
    left: 0;
    bottom: 0;
  }

  @include tablet {
    padding: 35px 17px 90px;
  }

  @include mobile {
    padding: 35px 17px 55px;
  }

  &__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-bottom: 35px;

    @include tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 22px;
      margin-bottom: 20px;
    }
  }

  &__wrapper-btn {
    position: absolute;
    width: 90%;
    left: 85px;
    bottom: 40px;

    @include tablet {
      width: 90%;
      left: 15px;
      bottom: 40px;
    }
  }

  &__list {
    position: relative;

    &::before {
      content: "";
      border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
      width: 44%;
      height: 4px;
      position: absolute;
      top: 85px;
      right: -75px;
      transform: rotate(90deg);
    }
  }

  &__item {
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: #000018;
    margin-bottom: 30px;
    padding-right: 20px;

    @include mobile {
      margin-bottom: 23px;
    }
  }

  &__item-weight {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    color: #000018;

    @include tablet {
      display: block;
    }
  }
}

.lk-tabs {

  &__panel-title {}
}

.discount {

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-bottom: 20px;

    @include mobile {
      font-size: 22px;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 65px;

    @include tablet {
      margin-bottom: 50px;
    }

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__item {
    background: #F2F2FA;
    box-shadow: 3px 3px 10px rgba(0, 0, 24, 0.07);
    border-radius: 30px;
    padding: 20px 40px 15px 85px;
    width: 31%;
    position: relative;

    @include big-desktop {
      padding: 15px 5px 15px 65px;
    }

    @include tablet {
      padding: 15px 5px 15px 60px
    }

    @include small-tablet {
      width: 100%;
      padding: 23px 20px 15px 80px;
    }

    &:not(:last-child) {
      margin-bottom: 15px;
    }

    &::before {
      content: '';
      background-repeat: no-repeat;
      width: 30px;
      height: 30px;
      fill: #313188;
      position: absolute;
      top: 20px;
      left: 40px;

      @include big-desktop {
        top: 15px;
        left: 20px;
      }

      @include tablet {
        width: 25px;
        height: 25px;
        background-size: 25px 25px;
      }

      @include small-tablet {
        top: 25px;
        left: 30px;
      }
    }
  }

  &__item-title {
    font-weight: 400;
    font-size: 16px;
    line-height: 32px;
    color: #000018;
    margin: 0;

    @include tablet {
      font-size: 14px;
    }
  }

  &__item-text {
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: #000018;
    margin: 0;

    @include tablet {
      font-size: 16px;
    }
  }

  &__item-link {
    font-weight: 500;
    font-size: 18px;
    line-height: 32px;
    color: #313188;

    @include tablet {
      font-size: 16px;
    }
  }

  &__item--star {
    &::before {
      background-image: url(../img/star.svg);
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
    }
  }
}

.is-hidden+.order__more-btn {

  &::after {
    content: "";
    top: 25%;
    transform: rotate(0deg);
  }
}

.lk-cash {

  @include desctop {
    position: absolute;
    right: 25px;
    top: 40px;
  }

  @include mobile {
    position: relative;
    right: 0;
    top: 0;
    padding: 4px 14px;
    margin-bottom: 15px;
    font-size: 10px;
  }
}
