.info {
  padding-bottom: 95px;

  @include big-desktop {
    padding-bottom: 70px;
  }

  @include tablet {
    padding-bottom: 58px;
  }

  @include small-tablet {
    padding-bottom: 40px;
  }

  &__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 64px;
    color: var(--text-color);
    margin: 0;
    margin-bottom: 40px;

    @include desctop {
      margin-bottom: 17px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 36px;
    }
  }

  &__text {
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
    color: var(--img-color);
    margin: 0;
    margin-bottom: 40px;

    @include big-desktop {
      font-size: 22px;
    }

    @include tablet {
      font-size: 20px;
      margin-bottom: 28px;
    }

    @include small-tablet {
      margin-bottom: 14px;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;

    @include big-desktop {
      margin-bottom: 37px;
    }

    @include desctop {
      flex-direction: column;
    }

    @include tablet {
      margin-bottom: 0;
    }
  }

  &__item {
    max-width: 333px;
    width: 100%;
    padding-right: 25px;
    position: relative;
    z-index: 1;

    @include big-desktop {
      padding-right: 40px;
    }

    @include desctop {
      max-width: 100%;
      width: 100%;
      padding-right: 0;
      display: flex;
      align-items: center;
      margin-bottom: 35px;
    }

    @include small-tablet {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 55px;
    }

    &:not(:last-child) {
      &::before {
        content: "";
        position: absolute;
        top: 25px;
        left: 135px;
        width: 100%;
        height: 3px;
        background-image: url(../img/line.svg);
        background-repeat: no-repeat;
        background-size: cover;
        z-index: -1;

        @include desctop {
          display: none;
        }
      }
    }
  }

  &__item-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 36px;
    color: var(--text-color);
    padding: 3px 30px;
    border: 3px solid var(--img-color);
    border-radius: 30px;
    display: inline-block;
    margin: 0;
    margin-bottom: 25px;
    background-color: var(--light-color);
    text-transform: uppercase;

    @include desctop {
      margin: 0;
      flex-shrink: 0;
      margin-right: 115px;
    }

    @include tablet {
      padding: 3px 29px;
      font-size: 18px;
    }

    @include small-tablet {
      margin-bottom: 20px;
    }
  }

  &__item-title--active {
    background-color: var(--img-color);
    color: var(--light-color);
  }

  &__item-text {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--text-color);
    margin: 0;

    @include big-desktop {
      font-size: 22px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 32px;
    }
  }

  &__item--email {
    font-size: 32px;
    line-height: 36px;
    color: var(--img-color);

    @include big-desktop {
      font-size: 30px;
    }

    @include tablet {
      font-size: 20px;
    }
  }
}

.info-tabs {

  &__nav {
    margin-bottom: 25px;
    flex-wrap: wrap;

    @include tablet {
      margin-bottom: 20px;
    }

    @include tablet {
      margin-bottom: 17px;
    }

  }

  &__nav-item {
    &:not(:last-child) {
      margin-right: 75px;

      @include big-desktop {
        margin-right: 25px;
      }

      @include small-tablet {
        margin-right: 11px;

      }
    }
  }

  &__nav-btn {
    background-color: var(--nor-btn);
    border-radius: 30px;
    padding: 7px 57px;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--text-color);
    border: 3px solid transparent;
    margin-bottom: 15px;

    @include big-desktop {
      padding: 3px 57px;
      font-size: 22px;
    }

    @include tablet {
      padding: 3px 25px;
      font-size: 20px;
    }

    @include small-tablet {
      padding: 3px 14px;
    }
  }

  & .tabs__nav-btn--active {
    border: 3px solid var(--img-color);
    border-radius: 30px;
    background-color: var(--light-color);

    &:focus {
      border: 3px solid var(--img-color);
      border-radius: 30px;
      background-color: var(--light-color);
      padding: 7px 57px;
      font-weight: 500;
      font-size: 24px;
      line-height: 36px;
      color: var(--text-color);

      @include big-desktop {
        padding: 3px 57px;
        font-size: 22px;
      }

      @include tablet {
        padding: 3px 25px;
        font-size: 20px;
      }

      @include small-tablet {
        padding: 3px 14px;
      }
    }
  }

  &__content {}

  &__panel {}
}
