.category-modal {

  &__container {
    padding: 40px 45px 20px;
  }

  &__close {}

  &__content {}

  &.is-open {
    background: rgba(70, 70, 70, 0.58);
    backdrop-filter: blur(20px);
  }

  &__descr {
    display: flex;
  }

  &__sale {
    position: relative;
    display: flex;
    align-items: flex-start;
    margin-bottom: 20px;

    @include mobile {
      flex-direction: column-reverse;
    }
  }

  &__sale-descr {
    margin-right: 40px;
    margin-top: 55px;

    @include mobile {
      margin-right: 0;
      margin-top: 20px;
    }
  }

  &__sum {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-bottom: 10px;
  }

  &__cashback {
    background-color: #FFE600;
    border-radius: 30px;
    padding: 4px 14px;
    font-weight: 400;
    font-size: 12px;
    color: var(--text-color);
    display: inline-block;

    @include big-desktop {
      font-size: 9px;
    }
  }

  &__total {
    font-weight: 700;
    font-size: 28px;
    line-height: 44px;
    color: #000018;
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;

    @include mobile {
      flex-direction: column;
    }
  }

  &__btns {
    display: flex;
    justify-content: flex-end;

    @include mobile {
      flex-direction: column;
    }
  }

}

.card__btn-light {
  border: 1px solid #313188;
  border-radius: 30px;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  border-radius: 30px;
  padding: 17px 28px;
  display: inline-block;
  color: #313188;
  margin-right: 20px;

  @include tablet {
    font-size: 16px;
    line-height: 19px;
    padding: 15px 28px;
  }

  @include small-tablet {
    padding: 10px 30px;
  }

  @include mobile {
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.category-modal__close {
  position: absolute;
  right: 40px;
  top: 43px;
  border: none;
  padding: 0;
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-image: url(../img/modal-close.svg);
  background-repeat: no-repeat;
  cursor: pointer;

  @include mobile {
    width: 25px;
    height: 25px;
    top: 23px;
    right: 20px;
    background-size: cover;
  }
}
