.video-block {
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  height: 580px;
  border-radius: 30px;
  margin-bottom: 50px;

  @include big-desktop {
    height: 485px;
    margin-bottom: 40px;
  }

  @include tablet {
    height: 300px;
    margin-bottom: 28px;
  }

  @include mobile {
    height: 230px;
  }


  &::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
    background-color: rgba(0, 0, 24, 0.4);
    backdrop-filter: blur(20px);
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &--played {
    &::before {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__content {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__play {
    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    width: 110px;
    height: 110px;
    transform: translate(-50%, -50%);
    transition: background-color 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    @include big-desktop {
      width: 85px;
      height: 75px;
    }

    @include tablet {
      width: 55px;
      height: 45px;
    }

    svg {
      width: 100%;
      height: 100%;
      fill: var(--light-color);
    }

    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    &:hover {
      background-color: var(--color-active);
    }

    &:active {
      background-color: var(--color-active);
    }

    &--played {
      opacity: 0;
      visibility: hidden;
    }
  }
}
