//     sass- -  root
// colors
:root {
  // base
  --font-family: "Acrom", sans-serif;
  --content-width: 1440px;
  --content-width-large: 1530px;
  --container-offset: 97px;
  --container-offset-tablet: 21px;
  --container-offset-mobile: 16px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width-large: calc(var(--content-width-large) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --text-color: #000018;
  --count-color: #ff0000;
  --img-color: #313188;
  --nor-btn: #f2f2fa;
  --lk-color: #f3f3ff;
  --hover: #4848ad;
}
