.hero {
  background-image: url(../img/hero-bg.png);
  min-height: 670px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-bottom: 60px;

  @include big-desktop {
    min-height: 590px;
    margin-bottom: 40px;
  }

  @include tablet {
    min-height: 450px;
    margin-bottom: 35px;
  }

  @include small-tablet {
    margin-bottom: 30px;
  }

  &__title {
    font-weight: 700;
    font-size: 64px;
    line-height: 87px;
    color: var(--light-color);
    margin: 0;
    margin-bottom: 100px;
    margin-top: 20px;

    @include big-desktop {
      font-size: 52px;
      line-height: 77px;
      margin-top: 30px;
      margin-bottom: 70px;
    }

    @include tablet {
      font-size: 36px;
      line-height: 48px;
      margin-left: 16px;
    }

    @include small-tablet {
      font-size: 24px;
      line-height: 36px;
      margin-left: 0;
      margin-top: 40px;
      margin-bottom: 45px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 36px;
    line-height: 43px;
    color: var(--light-color);
    display: block;
    margin-top: 20px;

    @include big-desktop {
      font-size: 32px;
      line-height: 38px;
      margin-top: 10px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 29px;
    }

    @include small-tablet {
      font-size: 16px;
      line-height: 19px;
    }
  }

  &__btn {
    background-color: var(--img-color);
    border-radius: 30px;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    padding: 16px 82px;
    color: var(--light-color);
    margin-left: 65px;
    flex: 1 0 auto;
    transition: background-color .3s ease-in ;

    @include big-desktop {
      margin-left: 28px;
    }

    @include tablet {
      font-size: 16px;
      line-height: 19px;
      padding: 15px 30px;
      margin-left: 20px;
    }

    @include small-tablet {
      width: 100%;
      margin-left: 0;
    }

    &:hover {
      background-color: var(--hover);
    }
  }
}

.hero-form {
  display: flex;
  justify-content: space-between;
  align-items: baseline;

  @include small-tablet {
    flex-direction: column;
  }

  &__label {
    border-bottom: 3px solid #FFFFFF;
    max-width: 990px;
    width: 70%;
    position: relative;

    @include big-desktop {
      max-width: 772px;
    }

    @include small-tablet {
      max-width: 100%;
      width: 100%;
      margin-bottom: 31px;
    }
  }

  &__input {
    background-color: transparent;
    padding: 15px 5px;
    width: 100%;
    color: var(--light-color);

    &:focus-visible {
      outline: transparent;
    }

    @include desctop {
      padding: 12px 40px 12px 5px;
    }

    @include small-tablet {
      padding-left: 0px;
    }

    &::placeholder {
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: var(--light-color);
      opacity: 0.5;

      @include tablet {
        font-size: 16px;
        line-height: 19px;
      }
    }
  }

  &__input-btn {
    position: absolute;
    top: 5px;
    right: 0;
  }

  &__input-img {
    width: 35px;
    height: 35px;
    fill: var(--light-color);
    opacity: 0.5;
  }
}
