.product-card {
  padding-top: 150px;

  @include desctop {
    padding-top: 140px;
  }

  @include small-tablet {
    padding-top: 80px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 60px;

    @include desctop {
      flex-direction: column;
      position: relative;
    }
  }

  &__info {
    width: 710px;

    @include desctop {
      width: 100%;
    }
  }

  &__numb {

    @include desctop {
      position: absolute;
      top: 0;
      left: 0;
      font-size: 28px;
    }

    @include mobile {
      margin-bottom: 50px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 44px;
    color: #000018;
    margin: 0;

    @include small-tablet {
      font-size: 24px;
      line-height: 40px;
    }
  }

  &__subtitle {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-bottom: 20px;

    @include small-tablet {
      font-size: 22px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: #000018;
    margin: 0;
    margin-bottom: 30px;
  }

  &__art {
    font-weight: 400;
    font-size: 18px;
    line-height: 36px;
    color: #D9D9D9;
    margin: 0;

    @include small-tablet {
      font-size: 16px;
    }
  }

  &__list {
    margin-top: 10px;

    @include small-tablet {
      margin-top: 0;
    }
  }

  &__item {
    display: flex;
    justify-content: space-between;
    font-weight: 400;
    font-size: 16px;
    line-height: 36px;
    color: #000018;
    position: relative;

    @include small-tablet {
      flex-direction: column;
      line-height: 26px;
    }

    &::before {
      content: "";
      border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
      width: 100%;
      height: 2px;
      position: absolute;
      bottom: -10px;
      left: 0;
    }

    &:not(:last-child) {
      margin-bottom: 25px;

      @include small-tablet {
        margin-bottom: 15px;
      }
    }
  }

  &__item-text {
    font-weight: 500;
    font-size: 18px;
    line-height: 36px;
    color: #000018;
    margin: 0;
  }

  &__quantity {
    background: #F2F2FA;
    box-shadow: 3px 3px 10px rgba(0, 0, 24, 0.07);
    border-radius: 30px;
    padding: 25px 45px 20px 25px;
    margin-top: 20px;
    position: relative;

    @include small-tablet {
      margin-top: 45px;
      padding: 32px 32px 20px 15px;
      text-align: center;
    }
  }

  &__share {

    & svg {
      width: 30px;
      height: 30px;
      position: absolute;
      top: 35px;
      right: 45px;

      @include small-tablet {
        top: 40px;
        right: 15px;
      }
    }

    & use {
      width: 100%;
      height: 100%;
      fill: #313188;
    }
  }

  &__remains {
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(20px);
    border-radius: 30px;
    font-weight: 400;
    font-size: 16px;
    line-height: 36px;
    color: #000018;
    padding: 5px 45px;
    display: inline-block;
    margin-bottom: 33px;

    @include small-tablet {
      padding: 5px 25px;
    }
  }

  &__total {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    position: relative;

    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }

    &::before {
      content: "";
      border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
      width: 100%;
      height: 2px;
      position: absolute;
      top: -15px;
      left: 0;
    }

    & .stepper {
      margin-top: 0;
      margin-left: 50px;

      @include small-tablet {
        order: -1;
        margin-left: 0;
        margin-bottom: 23px;
      }
    }
  }

  &__price {
    padding-left: 20px;

    @include small-tablet {
      padding-left: 0;
      margin-bottom: 20px;
    }
  }

  &__sum {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-bottom: 10px;

    @include small-tablet {
      margin-bottom: 3px;
    }
  }

  &__cashback {

    background-color: #FFE600;
    border-radius: 30px;
    padding: 4px 14px;
    font-weight: 400;
    font-size: 12px;
    color: var(--text-color);

    @include big-desktop {
      font-size: 9px;
    }
  }

  &__specific {}

  &__specific-wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;

    @include desctop {
      flex-direction: column;
    }

    & .product-card__list {
      flex: 1 0 auto;

      &:not(:last-child) {
        margin-right: 20px;
      }
    }
  }

  &__plan {
    margin-top: 60px;
    margin-bottom: 90px;
  }

  &__plan-btns {
    margin-top: 55px;
    margin-bottom: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include tablet {
      flex-wrap: wrap;
    }
  }

  &__plan-btn {
    border: 3px solid #313188;
    border-radius: 30px;
    padding: 4px;
    text-align: center;
    flex: 1 0 auto;
    font-weight: 400;
    font-size: 20px;
    line-height: 34px;
    position: relative;

    @include large-desktop {
      padding: 4px 25px;
    }

    @include big-desktop {
      text-align: left;
      padding-left: 25px;
      font-size: 18px;
      margin-bottom: 10px;
    }

    @include small-tablet {
      font-size: 16px;
    }

    &:not(:last-child) {
      margin-right: 40px;

      @include big-desktop {
        margin-right: 10px;
      }

      @include small-tablet {
        margin-right: 0;
      }
    }

    & use {
      fill: #313188;
      stroke: #fff;

    }

    & svg {
      width: 34px;
      height: 34px;
      position: absolute;
      top: 50%;
      right: 55px;
      transform: translateY(-50%);

      @include big-desktop {
        right: 20px;
      }
    }
  }
}

.card-slider {
  width: 720px;
  height: 552px;
  display: flex;
  margin-right: 15px;

  @include large-desktop {
    flex-direction: column-reverse;
    width: 490px;
    height: 485px;
    margin-bottom: 80px;
  }

  @include desctop {
    width: 100%;
    margin-top: 140px;
    margin-bottom: 10px;
    flex-direction: row;
    margin-bottom: 65px;
  }

  @include small-tablet {
    height: 415px;
    margin-top: 180px;
    margin-bottom: 10px;
  }
}

.card-info {
  width: calc(100% - 750px - 60px);
  padding-right: 62px;

  @include large-desktop {
    display: flex;
    width: 100%;
  }
}

.prods-slider {
  @include large-desktop {
    width: 100%;
  }
}

.thumbs-slider {
  overflow: hidden;
  width: 215px;
  margin-right: 10px;
  flex-shrink: 0;
  height: 500px;

  @include large-desktop {
    width: 100%;
    height: 100px;
  }

  @include desctop {
    height: 500px;
    width: 200px;
    margin-right: 50px;
  }

  @include small-tablet {
    display: none;
  }

  & img {
    max-height: 100%;
  }
}

.prods-slider {

  & .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  & .swiper-slide img {
    width: 100%;
  }


}




