.often-slider {
  background: #F2F2FA;
  box-shadow: 3px 3px 10px rgba(0, 0, 24, 0.07);
  border-radius: 30px;
  padding: 40px 45px 130px;
  margin: 75px -45px 90px;

  & .best-slider__pag {
    margin-bottom: 60px;
    margin-left: 45px;
    background-color: #D9D9D9;
    max-width: 75%;
  }

  & .best-slider__btn {
    margin-bottom: 60px;
    margin-right: 45px;
  }
}

.often-title {
  font-weight: 700;
  font-size: 28px;
  line-height: 64px;
  color: var(--text-color);
  margin: 0;
  margin-bottom: 50px;

  @include big-desktop {
    margin-bottom: 20px;
  }

  @include tablet {
    font-size: 24px;
    line-height: 36px;
  }
}

.card-slider .swiper-pagination {
  display: none;

  @include small-tablet {
    display: block;
  }
}
