.best {
  padding-bottom: 75px;

  @include big-desktop {
    padding-bottom: 68px;
  }

  @include tablet {
    padding-bottom: 55px;
  }

  @include small-tablet {
    padding-bottom: 75px;
  }

  &__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 64px;
    color: var(--text-color);
    margin: 0;
    margin-bottom: 50px;

    @include big-desktop {
      margin-bottom: 20px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 36px;
    }
  }
}

.best-slider {
  padding-bottom: 82px;
  position: relative;

  @include small-tablet {
    padding-bottom: 65px;
    text-align: center;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 44px;
    height: 10px;
    fill: none;
    stroke: var(--img-color);
    stroke-width: 3;
    transition: background-color .3s ease-in-out, color .3s ease-in-out;

    &.swiper-button-disabled {
      opacity: .4;
      pointer-events: none;
    }

    & svg {
      width: 44px;
      height: 15px;
    }
  }

  &__next {
    position: absolute;
    right: 0;
    bottom: 12px;
    z-index: 30;
  }

  &__prev {
    position: absolute;
    right: 125px;
    bottom: 12px;
    z-index: 30;
    transform: scaleX(-1);

    @include small-tablet {
      right: 70px;
    }
  }

  &__pag {
    display: flex;
    align-items: center;
    width: calc(100% - 210px) !important;
    background-color: #F3F3FF;
    height: 3px;

    @include small-tablet {
      display: none;
    }

    .swiper-pagination-bullet {
      border-radius: 30px;
      width: 100%;
      background-color: transparent;
      opacity: 1;
      margin: 0 !important;
    }

    .swiper-pagination-bullet-active {
      background-color: var(--img-color);
      height: 5px;
    }
  }
}

.card {
  position: relative;

  @include small-tablet {
    text-align: center;
  }

  &__cashback {
    background-color: #FFE600;
    border-radius: 30px;
    padding: 4px 14px;
    position: absolute;
    top: 0;
    right: 0;
    font-weight: 400;
    font-size: 12px;
    color: var(--text-color);

    @include big-desktop {
      font-size: 9px;
    }
  }

  &__image {
    padding-top: 25px;

    @include small-tablet {
      padding-top: 7px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--text-color);
    margin: 0;
    margin-top: 25px;
    margin-bottom: 10px;

    @include big-desktop {
      font-size: 22px;
      line-height: 36px;
      margin-bottom: 0;
    }

    @include tablet {
      font-size: 18px;
      line-height: 30px;
      margin-top: 0;
    }

    @include small-tablet {
      margin-top: 10px;
    }

    @include mobile {
      margin-top: 0;
    }
  }

  &__num {
    font-weight: 400;
    font-size: 18px;
    line-height: 36px;
    color: var(--text-color);
    margin-bottom: 10px;

    @include big-desktop {
      font-size: 18px;
      line-height: 36px;
    }

    @include tablet {
      font-size: 16px;
      line-height: 36px;
    }
  }

  &__descr {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 35px;

    @include big-desktop {
      flex-direction: column;
      align-items: flex-start;
      margin-top: 10px;
    }

    @include small-tablet {
      align-items: center;
    }
  }

  &__discount {
    position: relative;
  }

  &__sale {
    font-weight: 700;
    font-size: 20px;
    line-height: 36px;
    color: #D9D9D9;
    text-decoration: line-through;
    position: absolute;
    top: -30px;
    left: 0;

    &::before {
      content: "-20%";
      position: absolute;
      transform: translateY(-50%);
      top: 50%;
      right: -60px;
      display: inline-flex;
      align-items: center;
      padding: 10px;
      height: 10px;
      font-weight: 700;
      font-size: 10px;
      line-height: 12px;
      background-color: var(--count-color);
      border-radius: 20px;
      color: var(--light-color);
    }
  }

  &__sum {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: var(--text-color);
    margin: 0;

    @include big-desktop {
      font-size: 28px;
      line-height: 36px;
      margin-bottom: 20px;
    }

    @include tablet {
      font-size: 24px;
      line-height: 36px;
    }

    @include small-tablet {
      margin-bottom: 5px;
    }
  }

  &__btn {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: var(--light-color);
    background: var(--img-color);
    border-radius: 30px;
    padding: 15px 28px;
    border: 3px solid transparent;
    display: inline-block;
    transition: background-color .3s ease-in ;

    &:hover {
      background-color: var(--hover);
    }

    @include tablet {
      font-size: 16px;
      line-height: 19px;
      padding: 15px 28px;
    }

    @include small-tablet {
      padding: 10px 30px;
    }
  }
}
