.context {
  padding-top: 150px;
  padding-bottom: 90px;

  @include big-desktop {
    padding-bottom: 65px;
  }

  @include small-tablet{
    padding-top: 90px;
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 36px;
    margin: 0;
    margin-bottom: 35px;

    @include tablet {
      font-size: 28px;
      line-height: 44px;
      margin-bottom: 17px;
    }

    @include small-tablet {
      font-size: 24px;
      line-height: 38px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__image {
    border-radius: 30px;

    @include tablet {
      height: 165px;
    }

    @include small-tablet {
      height: 120px;
    }

    @include mobile {
      height: 200px;
    }
  }

  &__subtitle {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    margin: 0;
    margin-top: 65px;
    margin-bottom: 30px;

    @include tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 22px;
      margin-top: 60px;
      margin-bottom: 23px;
    }
  }
}

.context-slider {
  padding-top: 25px;
  padding-bottom: 75px;
  margin-bottom: 45px;

  @include big-desktop {
    padding-top: 0;
    padding-bottom: 60px;
    margin-bottom: 40px;
  }

  @include tablet {
    margin-bottom: 23px;
    text-align: center;
  }
}
