.category {
  padding-top: 150px;

  @include tablet {
    padding-top: 140px;
  }

  @include small-tablet {
    padding-top: 85px;
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-bottom: 45px;

    @include big-desktop {
      margin-bottom: 35px;
    }

    @include tablet {
      font-size: 28px;
    }

    @include small-tablet {
      font-size: 24px;
    }
  }

  &__filter {
    margin-bottom: 80px;

    @include big-desktop {
      margin-bottom: 60px;
    }

    @include small-tablet {
      margin-bottom: 30px;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 35px;
    margin-bottom: 40px;

    @include small-tablet {
      gap: 35px 0;
    }

    & .card__descr {
      @include large-desktop {
        flex-direction: column;
        margin-top: auto;
      }

      @include small-tablet {
        align-items: center;
      }
    }

    & .card__sale {
      position: relative;
      top: 0;
    }
  }

  &__card {
    grid-column: 3 span;

    @include large-desktop {
      display: flex;
      flex-direction: column;
    }

    @include tablet {
      grid-column: 4 span;

      &:nth-child(-n+7) {
        order: -3;
      }

      &:nth-child(8) {
        order: -4;
      }

      &:nth-child(n+11) {
        order: -1;
      }
    }

    @include small-tablet {
      grid-column: 12 span;
      text-align: center;
    }

    &:nth-child(n + 15) {
      display: none;
    }
  }

  &__line {
    grid-column: 12 span;
    position: relative;
    margin: 20px 0 10px;

    @include big-desktop {
      margin: 0;
    }

    @include tablet {
      display: none;
    }

    &::before {
      content: "";
      border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
      width: 100%;
      height: 2px;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }

  &__card-btn {
    grid-column: 12 span;
    text-align: center;
    margin-bottom: 90px;
    position: relative;

    &::before {
      content: "";
      border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
      width: 100%;
      height: 2px;
      position: absolute;
      top: 50%;
      left: 0;
    }
  }
}

.category-filter {

  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #000018;
    margin: 0;
    margin-bottom: 20px;
  }

  &__list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-bottom: 20px;

    @include small-tablet {
      &:last-child .category-filter__item {
        width: 100%;
        text-align: center;
        margin-right: 0;
      }
    }
  }

  &__title--sort {
    position: relative;
    margin-top: 45px;

    &::before {
      content: "";
      border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
      width: 100%;
      height: 2px;
      position: absolute;
      top: -35px;
      left: 0;
    }
  }

  &__item {
    margin-bottom: 15px;
    margin-right: 20px;

    @include tablet {
      margin-right: 15px;
    }
  }
}

.custom-checkbox {
  position: relative;
  padding: 7px 25px;
  display: block;

  @include small-tablet {
    padding: 4px 25px;
  }

  &__field {
    appearance: none;
    position: absolute;
  }

  &__content {
    cursor: pointer;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: var(--text-color);

    @include small-tablet {
      font-size: 16px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      background-color: var(--nor-btn);
      border-radius: 30px;
      border: 3px solid transparent;
      z-index: -1;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border: 3px solid var(--img-color);
      border-radius: 30px;
      background-color: var(--light-color);
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
      z-index: -1;
    }
  }
}

.custom-checkbox__field:checked+.custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:disabled+.custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

.category-bnr {
  position: relative;
  grid-column: 12 span;
  background: linear-gradient(132deg, #F2F2FA 30.03%, rgba(243, 243, 255, 0) 77.76%);
  border-radius: 30px;
  margin: 27px 0 40px;
  padding: 45px 50px;
  min-height: 305px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  @include big-desktop {
    overflow: hidden;
    margin: 27px 0 15px;
  }

  @include tablet {
    padding: 45px 35px;
  }

  @include small-tablet {
    padding: 40px 14px;
    padding-bottom: 200px;
  }

  &::after {
    content: "";
    background-image: url(../img/cat-1.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 66%;
    height: 100%;
    display: block;
    position: absolute;
    right: 0;
    top: 0px;

    @include big-desktop {
      width: 74%;
      height: 100%;
    }

    @include tablet {
      width: 78%;
      background-size: 90%;
      right: -125px;
      top: auto;
      bottom: -45%;
    }

    @include small-tablet {
      width: 600px;
      background-size: 90%;
      top: auto;
      bottom: -61%;
      right: -40px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 40px;
    line-height: 50px;
    color: #000018;
    margin: 0;
    margin-bottom: 15px;

    @include tablet {
      font-size: 30px;
    }

    @include small-tablet {
      font-size: 24px;
      line-height: 40px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 40px;
    color: #000018;
    margin: 0;

    @include tablet {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 18px;
      line-height: 28px;
    }
  }

  &__link {
    margin-top: auto;
    position: relative;
    font-weight: 500;
    font-size: 24px;
    line-height: 40px;

    @include tablet {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 18px;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 12px;
      right: -65px;
      width: 44px;
      height: 12px;
      background-image: url(../img/catalog-arr.svg);
      background-repeat: no-repeat;
      background-size: cover;

      @include big-desktop {
        bottom: 45px;
        left: 25px;
      }

      @include tablet {
        left: 105%;
        bottom: 12px;
      }
    }
  }
}

.category__card.is-visible {
  display: block;
}

.show-more {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #313188;
  background-color: var(--light-color);
  padding: 0 35px 0 20px;
  position: relative;
  text-transform: lowercase;

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    right: 12px;
    z-index: 1;
    background-image: url(../img/arr-show.svg);
    background-repeat: no-repeat;
    background-size: 12px 12px;
    transform: translateY(-50%);
    width: 12px;
    height: 12px;
    pointer-events: none;
  }
}
