.cart {
  padding-top: 150px;

  @include desctop {
    padding-top: 140px;
  }

  @include small-tablet {
    padding-top: 90px;
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-bottom: 25px;
    display: inline-block;
    margin-right: 10px;

    @include small-tablet {
      margin-right: 3px;
      margin-bottom: 7px;
      font-size: 24px;
    }
  }

  &__subtitle {
    font-size: 18px;
    line-height: 36px;
    color: #D9D9D9;

    @include small-tablet {
      font-size: 16px;
    }
  }

  &__form {
    margin-bottom: 100px;

    @include big-desktop {
      margin-bottom: 65px;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    position: relative;

    &:not(:last-child) {
      padding-bottom: 15px;
      margin-bottom: 6px;

      @include small-tablet {
        margin-bottom: 0;
      }

      &::before {
        content: "";
        border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
        width: 100%;
        height: 2px;
        position: absolute;
        bottom: 0;
        left: 0;
      }
    }
  }

  &__item-wrap {
    display: flex;
    width: 86%;

    @include desctop {
      flex-direction: column;
      width: 100%;
      margin-left: 40px;
    }

    @include small-tablet {
      margin-left: 25px;
    }
  }

  &__item-tdescr {
    display: flex;
    flex: 1 0 auto;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__item--total {

    @include desctop {
      margin-top: 20px;
    }

    & .cart__item-total {
      margin-left: 55px;
      text-align: right;
      max-width: 290px;
      width: 86%;
    }
  }

  &__item-pic {
    flex: 1 0 auto;

    @include small-tablet {
      flex: auto;
      width: 25%;
    }
  }

  &__item-descr {
    margin-left: 40px;
    margin-right: 75px;
    max-width: 620px;
    width: 100%;

    @include big-desktop {
      max-width: 500px;
      margin-left: 95px;
      margin-right: 0;
    }

    @include desctop {
      max-width: 100%;
      margin-left: 0;
      width: auto;
    }
  }

  &__item-title {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-top: 20px;
    margin-bottom: 10px;

    @include big-desktop {
      margin-top: 7px;
      margin-bottom: 0;
    }

    @include small-tablet {
      font-size: 14px;
      line-height: 26px;
    }
  }

  &__item-text {
    font-weight: 700;
    font-size: 18px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-left: auto;
    align-self: baseline;
  }

  &__item-btn {
    font-weight: 400;
    font-size: 18px;
    line-height: 36px;
    color: #D9D9D9;
    display: flex;
    justify-content: flex-start;


    @include small-tablet {
      font-size: 14px;
    }

    & use {
      fill: #D9D9D9;
    }

    & svg {
      width: 30px;
      height: 30px;
      margin-right: 10px;
    }
  }

  &__item-quantity {
    text-align: center;

    @include big-desktop {
      text-align: left;
    }

    @include small-tablet {
      margin-bottom: 10px;
    }

    & .stepper {
      margin-top: 15px;
      margin-left: -40px;

      @include desctop {
        margin-left: 0;
        width: 145px;
        height: 35px;
      }
    }
  }

  &__item-stepper-quan {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    color: #D9D9D9;

    @include big-desktop {
      margin-top: 6px;
    }
  }

  &__item-total {
    margin-left: auto;

    @include small-tablet {
      margin-left: 0;
      margin-top: 10px;
    }
  }

  &__item-sale {
    font-weight: 700;
    font-size: 20px;
    line-height: 36px;
    color: #D9D9D9;
    margin-left: -20px;
    position: relative;
    text-decoration: line-through;

    @include small-tablet {
      margin-left: 0px;
      font-size: 16px;
    }

    &::before {
      content: "-20%";
      position: absolute;
      transform: translateY(-50%);
      top: 50%;
      right: -60px;
      display: inline-flex;
      align-items: center;
      padding: 10px;
      height: 10px;
      font-weight: 700;
      font-size: 10px;
      line-height: 12px;
      background-color: var(--count-color);
      border-radius: 20px;
      color: var(--light-color);
    }
  }

  &__item-sum {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: #000018;
    margin: 0;

    @include small-tablet {
      font-size: 20px;
      line-height: 28px;
    }
  }

  &__item-cashback {
    background-color: #FFE600;
    border-radius: 30px;
    padding: 7px 14px;
    font-weight: 400;
    font-size: 11px;
    color: var(--text-color);
    margin: 0;
    margin-bottom: 5px;
    display: inline-block;

    @include big-desktop {
      font-size: 10px;
      padding: 4px 14px;
    }
  }
}

.cart-form {

  &__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: #000018;
    margin: 0;
    margin-bottom: 30px;

    @include desctop {
      font-size: 24px;
    }

    @include small-tablet {
      font-size: 22px;
    }
  }

  &__top {
    background: #F2F2FA;
    box-shadow: 3px 3px 10px rgba(0, 0, 24, 0.07);
    border-radius: 30px;
    padding: 30px 45px 47px;
    margin-top: 25px;
    margin-left: -45px;
    margin-right: -45px;
    margin-bottom: 45px;

    @include big-desktop {
      margin-bottom: 20px;
    }

    @include desctop {
      margin-left: 0;
      margin-right: 0;
      margin-top: 30px;
      padding: 30px 15px 47px;
    }

    @include small-tablet {
      padding: 45px 15px 47px;
    }
  }

  &__order {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;

    @include desctop {
      flex-wrap: wrap;
      margin-bottom: 35px;
    }
  }

  &__label {
    background: rgba(255, 255, 255, 0.8);
    flex: 1 0 auto;
    border-radius: 30px;

    @include mobile {
      width: 100%;
    }

    &:not(:last-child) {
      margin-right: 35px;

      @include mobile {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }

    &:first-child {
      @include desctop {
        width: 100%;
        margin-bottom: 15px;
      }
    }
  }

  &__input {
    padding: 12px 17px;
    border-radius: 30px;
    width: 100%;
    border: 3px solid transparent;
  }

  &__order-wrapp {
    display: flex;
    flex-direction: column;
  }

  &__tabs {
    margin-bottom: 30px;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__tabs-item {

    &:not(:last-child) {
      margin-right: 100px;

      @include small-tablet {
        margin-right: 0;
        margin-bottom: 15px;
      }
    }
  }

  &__tabs-btn {
    position: relative;
    padding-left: 58px;
    font-size: 18px;
    line-height: 30px;
    color: #000018;

    &:focus {
      color: inherit;
      background-color: transparent;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background: rgba(255, 255, 255, 0.8);
      border: 3px solid #D9D9D9;
      backdrop-filter: blur(20px);
      width: 30px;
      height: 30px;
      border-radius: 10px;
    }

    &.tabs__nav-btn--active {
      position: relative;

      &::before {
        content: "";
        box-shadow: 0px 0px 3px 2px rgba(217, 217, 217, 0.2) inset;
        border: 3px solid #313188;
      }

      &::after {
        content: "";
        background-color: #313188;
        width: 20px;
        height: 20px;
        position: absolute;
        left: 5px;
        top: 5px;
        border-radius: 6px;
      }
    }
  }

  &__bottom {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 36px;
    margin-left: -45px;
    margin-right: -45px;

    @include desctop {
      margin-left: 0;
      margin-right: 0;
      gap: 20px;
    }
  }

  &__item {
    background: #F2F2FA;
    box-shadow: 3px 3px 10px rgb(0 0 24 / 7%);
    border-radius: 30px;
    padding: 30px 45px 25px;
    flex: 1 0 auto;

    @include desctop {
      padding: 40px 15px 25px;
    }

    @include small-tablet {
      padding: 20px 15px 25px;
    }
  }

  &__self {
    margin-left: -45px;
    margin-right: -45px;
    margin-bottom: 45px;
    margin-bottom: 520px;
    position: relative;
    z-index: 1;
    position: relative;

    @include tablet {
      margin-left: 0;
      margin-right: 0;
    }

    & .payment__radio::before {
      content: "";
      border-bottom: 2px dashed rgba(49, 49, 136, 0.08);
      width: 100%;
      height: 40%;
      position: absolute;
      top: 0;
      right: -270px;
      transform: rotate(90deg);

      @include small-tablet {
        display: none;
      }
    }

    @include big-desktop {
      margin-bottom: 480px;
    }

    @include small-tablet {
      margin-bottom: 435px;
    }

    & .payment__radio {
      padding-top: 10px;
    }

    & .payment-list__text {
      padding-left: 55px;
      margin-left: 0;
      margin-bottom: 55px;

      &::before {
        width: 26px;
        height: 26px;
        top: 0;
        transform: translateY(0);
      }

      &::after {
        top: 6px;
        left: 6px;
        transform: translateY(0);
      }

      & .cart-form__item-text {
        display: block;
      }
    }

  }

  &__item--payment {
    grid-column: span 7;

    @include desctop {
      grid-column: span 12;
    }
  }

  &__item--order {
    grid-column: span 5;

    @include desctop {
      grid-column: span 12;
    }
  }

  &__payment-list {
    margin-top: 38px;

    @include small-tablet {
      margin-top: 0;
    }
  }

  &__item-text {
    font-size: 16px;
    line-height: 30px;
    color: #000018;
    margin: 0;
    margin-bottom: 20px;
    font-style: normal;
  }

  &__item-quantity {
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    color: #000018;
  }

  &__item-total {
    font-weight: 700;
    font-size: 18px;
    line-height: 36px;
    color: #000018;
    display: flex;
    justify-content: space-between;
    margin: 0;
    margin-top: 55px;
    margin-bottom: 25px;

    @include small-tablet {
      flex-direction: column;
      align-items: flex-end;
      margin-bottom: 15px;
    }
  }

  &__item-sum {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    color: #000018;
  }

  &__item-label {
    display: block;
    margin-right: 0 !important;
    margin-bottom: 25px;

    @include small-tablet {
      margin-bottom: 10px;
    }
  }

  &__item-input {
    display: flex;
    width: 100%;
  }

  &__item-btn {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: var(--light-color);
    background: var(--img-color);
    border-radius: 30px;
    padding: 15px 28px;
    border: 3px solid transparent;
    display: block;
    width: 100%;
    margin-bottom: 10px;

    @include small-tablet {
      font-size: 16px;
    }
  }

  &__item-info {
    font-size: 12px;
    line-height: 22px;
    color: #000018;

    @include mobile {
      display: block;
      text-align: center;
    }
  }
}

.order-radio__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
  flex-wrap: wrap;

  @include small-tablet {
    margin-bottom: 0;
  }
}

.order-radio {
  margin-right: 100px;
  display: inline-block;
  margin-bottom: 10px;

  @include big-desktop {
    margin-right: 20px;
  }

  @include desctop {
    margin-right: 10px;
  }

  @include small-tablet {
    margin-right: 0;
    width: 100%;
    margin-bottom: 20px;
  }

  & input[type=radio]:disabled {
    background: #efefef;
    color: #666;
  }

  &__text {
    padding: 7px 30px;
    background: rgba(217, 217, 217, 0.5);
    border-radius: 30px;
    position: relative;
    z-index: 1;
    font-size: 18px;
    line-height: 30px;
    color: #000018;
    display: inline-block;

    @include desctop {
      font-size: 15px;
    }

    @include small-tablet {
      width: 100%;
    }

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      border: 3px solid #313188;
      border-radius: 30px;
      background: #F2F2FA;
      opacity: 0;
      color: #000018;
      z-index: -1;
    }
  }
}

.order-radio input:checked+.payment-list__text::after {
  opacity: 1;
}

.order-radio input:checked+.order-radio__text::after {
  opacity: 1;
}

.custom-checkbox input:checked+.custom-checkbox__content::after {
  opacity: 1;
}

.payment__radio {
  margin-right: 30px;
  margin-bottom: 25px;

  @include small-tablet {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.payment-list {

  &__item {
    position: relative;
  }

  &__text {
    position: relative;
    font-size: 18px;
    line-height: 36px;
    color: #000018;
    padding-left: 65px;
    margin-left: 35px;

    @include small-tablet {
      padding-left: 40px;
      margin-left: 0;
      font-size: 16px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
      background: rgba(255, 255, 255, 0.8);
      border: 3px solid #313188;
      backdrop-filter: blur(20px);
      border-radius: 30px;

      @include small-tablet {
        top: 14%;
      }
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 30px;
      height: 30px;
      background: #313188;
      backdrop-filter: blur(20px);
      border-radius: 30px;
      opacity: 0;
      border: 3px solid #313188;
      box-shadow: inset 0 0 0 2.5px #fff;

      @include small-tablet {
        top: 14%;
      }
    }
  }
}

.payment-btns {
  display: flex;
  flex-wrap: wrap;

  @include mobile {
    margin-bottom: 30px;
  }
}

.payment-item {
  &:not(:last-child) {
    margin-right: 15px;

    @include mobile {
      margin-bottom: 15px;
    }
  }
}

.payment-btn {
  padding: 7px 30px;
  background: rgba(217, 217, 217, 0.5);
  border-radius: 30px;
  font-size: 18px;
  line-height: 30px;
  color: #000018;
  display: inline-block;
  border: 3px solid transparent;

  &:not(:last-child) {
    margin-bottom: 35px;
  }

  &.tabs__nav-btn--active {
    border: 3px solid #313188;
    border-radius: 30px;
    background: #F2F2FA;
    color: #000018;
  }
}
