.catalog {

  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 36px;
    padding-bottom: 50px;

    @include big-desktop {
      gap: 30px;
    }

    @include tablet {
      gap: 20px;
      padding-bottom: 35px;
    }

    @include small-tablet {
      gap: 15px;
    }
  }

  &__item {
    display: block;
    height: 100px;
    background: #F2F2FA;
    box-shadow: 3px 3px 10px rgba(0, 0, 24, 0.07);
    border-radius: 30px;
    padding: 35px;
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--text-color);
    min-height: 220px;
    overflow: hidden;
    position: relative;
    z-index: 1;

    @include big-desktop {
      font-size: 22px;
      line-height: 34px;
      min-height: 180px;
      padding: 25px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 32px;
      padding: 20px;
    }

    &::after {
      content: "";
      z-index: -1;
    }

    &::before {
      content: "";
      position: absolute;
      bottom: 57px;
      left: 36px;
      width: 44px;
      height: 12px;
      background-image: url(../img/catalog-arr.svg);
      background-repeat: no-repeat;
      background-size: cover;

      @include big-desktop {
        bottom: 45px;
        left: 25px;
      }

      @include tablet {
        left: 15px;
      }
    }

    &:nth-child(1) {
      transition: filter .3s ease-in;

      &::after {
        content: "";
        background-image: url(../img/catalog/image-1.png);
        background-repeat: no-repeat;
        background-size: cover;
        filter: grayscale(100%);
        width: 246px;
        height: 253px;
        display: block;
        position: absolute;
        right: -37px;
        top: 92px;

        @include big-desktop {
