  &__item-image {
    position: absolute;
    right: -10%;
    top: 42%;
    filter: grayscale(1);

    &:hover {
      filter: grayscale(0);
    }
  }

  &__item--small {
    grid-column: span 3;

    @include desctop {
      grid-column: span 6;
    }

    @include small-tablet {
      grid-column: span 12;

      & br {
        display: none;
      }
    }
  }

  &__item--big {
    grid-column: span 6;

    @include tablet {
      grid-column: span 12;

      & br {
        display: none;
      }
    }
  }

  &__item--vert {
    grid-row: 2 span;
    grid-column: 3 span;
    min-height: 100%;

    @include desctop {
      grid-column: 6 span;
      grid-row: 1 span;
    }

    @include small-tablet {
      grid-column: 12 span;
      min-height: 180px;
    }
  }

  &__title {
    grid-column: span 12;
    margin: 0;
    margin-top: 26px;

    @include big-desktop {
      font-size: 22px;
      line-height: 36px;
      margin-top: 15px;
    }

    @include tablet {
      font-size: 20px;
      line-height: 36px;
      margin-top: 39px;
    }

    @include small-tablet {
      margin-top: 30px;
    }
  }
}

.catalog-help {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 80px;
  flex-wrap: wrap;

  @include big-desktop {
    padding-bottom: 65px;
  }

  @include tablet {
    padding-bottom: 80px;
  }

  @include small-tablet {
    padding-bottom: 60px;
  }

  &__text {
    font-weight: 700;
    font-size: 20px;
    line-height: 36px;
    color: var(--text-color);

    @include tablet {
      font-size: 18px;
      line-height: 36px;
      margin: 0;
      margin-bottom: 20px;
    }
  }

  &__links {

    @include tablet {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
    }
  }

  &__message {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--text-color);
    border: 3px solid var(--img-color);
    border-radius: 30px;
    padding: 10px 145px 10px 55px;
    position: relative;
    transition: background-color .3s ease-in ;

    &:hover {
      background-color: var(--nor-btn);
    }

    @include tablet {
      font-size: 16px;
      line-height: 19px;
      padding: 10px 175px 10px 95px;
    }

    @include small-tablet {
      width: 100%;
      padding: 10px 65px;
      margin-bottom: 10px;
    }

    &::before {
      content: "";
      position: absolute;
      right: 58px;
      top: 12px;
      width: 30px;
      height: 27px;
      background-image: url(../img/telegram.svg);
      background-repeat: no-repeat;
      background-size: cover;

      @include tablet {
        right: 95px;
        top: 8px;
        width: 25px;
        height: 22px;
      }

      @include small-tablet {
        right: 65px;
      }
    }
  }

  &__video {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    color: var(--light-color);
    background: var(--img-color);
    border-radius: 30px;
    padding: 10px 145px 10px 55px;
    border: 3px solid transparent;
    margin-left: 40px;
    position: relative;
    transition: background-color .3s ease-in ;

    &:hover {
      background-color: var(--hover);
    }

    @include tablet {
      font-size: 16px;
      line-height: 19px;
      padding: 10px 130px 10px 50px;
      margin-left: 15px;
    }

    @include small-tablet {
      width: 100%;
      padding: 10px 18px;
      margin-bottom: 10px;
      margin-left: 0;
    }

    &::before {
      content: "";
      position: absolute;
      right: 58px;
      top: 12px;
      width: 30px;
      height: 27px;
      background-image: url(../img/vid.svg);
      background-repeat: no-repeat;
      background-size: cover;

      @include tablet {
        right: 50px;
        top: 8px;
        width: 26px;
        height: 22px;
      }

      @include small-tablet {
        right: 17px;
      }
    }
  }
}
